
(function(ns){
  'use strict'


  class HtmlRenderer {
    //Slider
    static renderSlider(id) {
      var elem = document.getElementById(id);
      var input = document.createElement('input');
      input.setAttribute('type', 'range');
      input.id = id + '_sliderControl';
      input.min = 0;
      input.max = 100;
      input.step = 10;
      input.value = 50;
      elem.appendChild(input);
    }

    //Push Button
    static renderButton(id, text, handler) {
      var elem = document.getElementById(id);
      var input = document.createElement('input');
      input.setAttribute('type', 'button');
      input.addEventListener('click', handler, false);
      input.value = text;
      elem.appendChild(input);
      return input;
    }
  }

  class AudioComponent {
    constructor(id, settings, ctx) {
      this.id = id;
      this.settings = settings;
      this.ctx = ctx;
    }
  }

  class Oscillator extends AudioComponent{

    constructor(id, settings, ctx) {
      super(id, settings, ctx);

      this._osc = ctx.createOscillator();
      this._gain = ctx.createGain();
      this.ctx = ctx;
    }

    connect(node) {
      this._gain.connect(node);
    }

    disconnect(node) {
      this._gain.disconnect(node);
    }

    start() {
      this._osc.start();
    }

    init() {
      //Oscillator
      this._osc.type = this.settings.type;
      this._osc.frequency.value = this.settings.frequency;

      //Gain
      this._gain.gain = this.settings.gain;
      this._osc.connect(this._gain);

      return this._osc;
    }



    render() {
      HtmlRenderer.renderSlider(this.id);
      var btn = HtmlRenderer.renderButton(this.id, 'on/off', (e) => {
        var elem = e.target;
        if(elem.getAttribute('data-mute') == 'true') {
          this._gain.connect(this.ctx.destination);
          elem.setAttribute('data-mute', 'false');
        } else {
          this._gain.disconnect(this.ctx.destination);
          elem.setAttribute('data-mute', 'true');
        }
      });
      btn.setAttribute('data-mute', 'true');
    }
  }


  //debugger;

  window.onload = function() {
    var ctx = new AudioContext();
    var osc = new Oscillator('Osc1', {type: 'sine', frequency: 70, gain: 1}, ctx);
    //debugger;
    osc.render();
    //osc.connect(ctx.destination);
    osc.init();
    osc.start();
  }
})(window.ns || {});
